.stars {
  display: flex;

  &__star {
    background-image: url(/images/star.svg);
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-repeat: no-repeat;
    background-position: center;

    &:nth-child(-n+4) {
      background-image: url(/images/star-active.svg);
    }
  }
}
